import "./globals.css";
import Navbar from "@/components/layout/header/Navbar";
import Footer from "@/components/layout/Footer";
import { ThemeProvider } from "next-themes";
import TopBar from "@/components/layout/header/Topbar";
import MainHeader from "@/components/layout/header/MainHeader";
import ScrollToTop from "./scroll-reset";
import dynamic from "next/dynamic";

const Providers = dynamic(() => import("@/components/providers").then(mod => ({ default: mod.Providers })), { ssr: false });

export const metadata = { title: "Modern Clinic", description: "Rediscover Your Confidence" };

export default function RootLayout({ children }) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body>
        <Providers>
          <ThemeProvider
            attribute="class"
            defaultTheme="light"
            enableSystem={false}
          >
            <ScrollToTop />
            <TopBar />
            <MainHeader />
            {/* <Navbar /> */}
            {children}
            <Footer />
          </ThemeProvider>
        </Providers>
      </body>
    </html>
  );
}
